import React from 'react';
import { Container } from 'reactstrap';
import Fade from 'react-reveal/Fade';

const navbar = () => {
  return (
    <div className="fixed-top shadow bg-white">
      <Container>
        <nav className="navbar navbar-expand-md d-flex justify-content-between align-items-center">
          <a href="#" className="navbar-brand text-dark"><Fade left><h3>Everything</h3></Fade></a>
          <ul className="navbar-nav">
            <Fade left><li className="nav-item"><a href="#"target="_blank" className="nav-link">Home</a></li></Fade>
            <Fade top><li className="nav-item"><a href="#"target="_blank" className="nav-link">About</a></li></Fade>
            <Fade bottom><li className="nav-item"><a href="#"target="_blank" className="nav-link">Portfolio</a></li></Fade>
            <Fade right><li className="nav-item"><a href="#"target="_blank" className="nav-link">Facebook</a></li></Fade>
          </ul>
        </nav>
      </Container>
    </div>
  );
};

export default navbar;